@font-face {
  font-family: "knuckledown";
  src: local("knuckledown"), url(./Fonts/knuckledown.ttf) format("truetype");
}

* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth !important;
}

.nav-brand {
  height: 75px;
  padding-left: 25%;
}

.nav-colored {
  background-color: rgba(255, 255, 255, 0);
}
.nav-transparent {
  background-color: transparent;
}
.navbar {
  position: fixed;
  z-index: 500;
  width: 100vw;
  transition: all ease-out;
}
.navbar-light .navbar-toggler {
  color: rgba(0, 0, 0, 0.5);
  border-color: rgb(0 0 0 / 0%);
}
.navbar-light {
  background-color: #fcfcfc !important;
}
.nav-item {
  font-size: 18px !important;
  font-weight: bolder !important;
  padding-right: 20px !important;
}

button:focus {
  outline: 0px dotted;
  outline: 0px auto -webkit-focus-ring-color;
}

.float-right-nav-ul {
  align-items: center;
}
.section_one {
  position: relative;
  width: auto;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url(/src/img/bg/bg.jpg);
  background-size: cover;
}

.section_one .content {
  margin-top: 15%;
  font-family: knuckledown;
  font-weight: 900;
  color: rgb(252, 252, 252) !important;
  display: flex;
  justify-content: center;
  align-items: center;
}
.section_one h2 {
  font-size: 60px;
}
.change-color {
  color: rgb(255, 0, 242);
}
.section_one_arrow {
  cursor: POINTER;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
#ABOUT_US {
  background: rgb(255, 107, 248);
}

@media only screen and (min-width: 992px) {
  .hamburger {
    display: none;
  }
  .float-right-nav-ul {
    margin-left: auto;
    margin-right: 0 !important;
    padding-right: 5%;
  }
}
