iframe {
  position: relative;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 30vh !important;
  z-index: 100;
}

/*--*/
.address {
  margin-top: -50px !important;
}
.section_four {
  position: relative;
  width: auto;
  min-height: auto;
  background: linear-gradient(50deg, #f3b0dc, #f8aee1);
}
.section_four p,
.section_four i {
  color: #4059ff;
}
.section_four p {
  padding: 1% !important;

  margin-bottom: 0rem;
}
.section_four a {
  text-decoration: none !important;
}
.section_four p:hover {
  color: red;
}
/*--*/
footer .card {
  margin-top: -5px;
  background: rgb(90, 90, 90);
}
