/*-------------------------------------TABLET---*/
@media only screen and (max-width: 768px) {
  footer .card-title {
    margin-left: -6%;
  }
  footer .nav-brand {
    height: 120px;
    padding-left: 10%;
  }
}
/*-------------------------------------MOBILE---*/
@media only screen and (max-width: 600px) {
  .col-md-4 {
    margin-top: 30px;
  }
  .hdser {
    color: #4059ff;
    display: flex;
    justify-content: center;
    margin-top: 80px;
    font-family: "knuckledown";
    font-size: xxx-large;
  }
  .section_two {
    position: relative;
    /* height: auto; */
    width: auto;
    min-height: 150vh !important;
  }
  .nav-brand {
    height: 60px;
    padding-left: 10%;
  }
  .formse3 {
    margin-left: auto;
    margin-right: auto;
    width: 90%;
  }
  .nav-item {
    font-size: 18px !important;
    font-weight: bolder !important;
    padding-right: 0px !important;
  }
  footer .card-title {
    margin-left: 0;
  }
  footer .nav-brand {
    height: 80px;
    padding-left: 10%;
  }
}
